<template>
	<div class="discuss-container">
		<div class="discuss-header">
			<div class="discuss-zhong">
				<div class="discuss-quxiao">
					<img src="../assets/nav_back.png" @click="back()">
				</div>
				<div class="discuss-ti">{{special_name}}</div>
				<div></div>
			</div>
		</div>
		<div class="discuss-content">
			<div v-for=" ( item ,index )  in discussion_list"  :key="index">
				<!--<div v-if="data.user_vip == '3' || data.user_vip == '2' || data.user_vip == '1'">
					<div class="discuss-di">
						<div class="discuss-chuqi">{{item.toptitle_title}}</div>
						<div class="discuss-wap">
							<div class="discuss-around"  v-for=" i in toptags_list"  v-if=" i.toptag_toptitle_id == item.toptitle_id "  >
								<div class="discuss-xiang" @click="go_prologue(i.toptag_id,i.toptag_name)">{{i.toptag_name}}</div>
							</div>
						</div>
					</div>
				</div>-->
				<div class="discuss-di">
					<div class="discuss-chuqi">{{item.toptitle_title}}</div>
					<div class="discuss-wap">
						<div class="discuss-around"  v-for=" i in toptags_list"  v-if=" i.toptag_toptitle_id == item.toptitle_id "  >
							<div class="discuss-xiang" @click="go_prologue(i.toptag_id,i.toptag_name)">{{i.toptag_name}}</div>
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import Qs from "qs"
	export default {
		name: 'home',
		data() {
			return {
				msg: '',
				specialid: '',
				discussion_list: [],
				data: [],
				toptitleid: '',
				toptags_list: []
			}
		},
		methods: {
			back() {
				window.history.back();
			},
			go_prologue(toptag_id,toptag_name){
				this.$router.push({
					path:'prologue',
					query:{
						type:toptag_id,
						typeone:toptag_name
					}
				})
			},
			toptitles() {
				var that = this;
				var params = {
					specialid: that.special_id
				};
				that.$http.post('/special/toptitles', Qs.stringify(params), {

				}).then((res) => {
					if(res.meta.code == 200) {
						that.discussion_list = res.data;
						that.toptitle_id = res.data.toptitle_id;
						this.toptags();
					}
				})
			},
			toptags() {
//				console.log("111")
				for(var i=0; i < this.discussion_list.length;i++){
//					console.log(this.discussion_list[i]  )
//					console.log("4444")
					var that = this;
					var params = {
						toptitleid: that.discussion_list[i].toptitle_id,
					};
					that.$http.post('/special/toptags', Qs.stringify(params), {
	
					}).then((res) => {
//						console.log(res)
						if(res.meta.code == 200) {
//							let item = {"list":}
							that.toptags_list = that.toptags_list.concat(res.data);
//							console.log( that.toptags_list )
	//						that.toptitleid = res.data.toptitle_id;
						}
					})
				}
				
				
			}
		},
		created() {
			// 页面刚加载的时候执行
			if(!!localStorage.getItem('user_info')) {
				this.user_info = JSON.parse(localStorage.getItem('user_info'));
			}
			this.special_id = this.$route.query.type;
			this.special_name = this.$route.query.typeone;
			this.toptitles();
//			

		},
		mounted() {
			// 页面刚加载完 dom 也加载完 的时候执行

		}
	}
</script>

<style scoped>
	.discuss-container {
		width: 100%;
		height: 100%;
		background: #FFFFFF;
	}
	
	.discuss-header {
		height: 50px;
		background: #FFFFFF;
		line-height: 50px;
		border-bottom: 1px solid #eaeaea;
	}
	
	.discuss-zhong {
		display: flex;
		justify-content: space-between;
	}
	
	.discuss-quxiao img {
		width: auto;
		height: 1.3rem;
		margin-left: 1.5rem;
		margin-top: 1rem;
	}
	
	.discuss-ti {
		font-size: 1.8rem;
		color: #000000;
		margin-left: -1.5rem;
		font-weight: 550;
	}
	
	.discuss-content {
		width: 100%;
		height: calc(100% - 50px);
		overflow-x: hidden;
		overflow-y: auto;
	}
	
	.discuss-chuqi {
		font-size: 1.5rem;
		color: #000000;
		border-left: 5px solid #fd5997;
		padding-left: 0.8rem;
		margin-left: 1.5rem;
	}
	
	.discuss-di {
		padding-top: 2rem;
		/*margin-bottom: 0.5rem;*/
		border-bottom: 5px solid #EAEAEA;
	}
	.discuss-wap{
		display: flex;
		flex-wrap: wrap;
	}
	.discuss-around {
		display: flex;
		justify-content: space-around;
		width: 33.33%;
		padding-top: 1rem;
		padding-bottom: 1rem;
	}
	
	.discuss-bottom {
		padding-bottom: 2rem;
	}
	
	.discuss-xiang {
		font-size: 1.4rem;
		color: #7f7f7f;
	}
	
	.discuss-pt {
		padding-top: 2rem;
	}
	
	.discuss-bj {
		border-bottom: 5px solid #EAEAEA;
	}
</style>


